/* Styling for the reservation page container */
.reservation-page {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    background-color: #f4f4f4;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Heading styles */
  h1 {
    text-align: center;
    margin-bottom: 20px;
    color: black !important; /* Force the heading to appear in black */
  }
  
  /* Form group styling for spacing */
  .form-group {
    margin-bottom: 15px;
  }
  
  /* Label styling */
  label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #555;
  }
  
  /* Input field styling */
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  input[type="date"],
  input[type="number"] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  /* Focus styles for inputs */
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="tel"]:focus,
  input[type="date"]:focus,
  input[type="number"]:focus {
    border-color: #007bff;
    outline: none;
  }
  
  /* Button styling */
  .book-table-btn {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* Button hover effect */
  .book-table-btn:hover {
    background-color: #0056b3;
  }
  
  /* Error message styling */
  .error {
    color: red;
    font-size: 14px;
    margin-top: 5px;
  }
  